.container{
    margin: auto;
    max-width: 600px;
    display: flex;
    flex-direction: column;
    .title{
        text-align: center;
        color: crimson;
        font-size: 50px;
    }

    & h2{
        color: blueviolet;
    }
}

.inputForm{
    width: 100%;
    height: 40px;
    display: flex;
    border: 3px solid rgb(139, 226, 203);

    &>input{
        height: 38px;
        width: 80%;
        padding-left: 20px;
        outline: none;
        border: none;
        flex-grow: 1;
        font-size: 20px;
    }
    
    #addBtn{
        height: 40px;
        width: 60px;
        border: none;
        background:rgb(125, 42, 208);
        color: aliceblue;
        font-weight: 700;
        cursor: pointer;
    }
}

.deleteBtn{
    padding: 10px 10px;
    background:red;
    float: right;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}